#menu{margin-top:20px;height:50px}#menu ul{margin:0;padding:0}#menu ul li{list-style:none;display:inline-block;width:33.33%}#menu ul li a{color:white;font-size:18px;text-decoration:none}#menu ul li a:hover{cursor:pointer}.belt{width:30px;background-color:white;height:5px;margin-top:5px;margin-left:auto;margin-right:auto}@media (min-width: 768px) and (orientation: portrait){#menu ul li a{font-size:25px}}@media (min-width: 1024px) and (orientation: landscape){#menu ul li{width:13.33%}#menu ul li a{font-size:25px}}
