#form{width:100%;height:130px;background-color:#1f1cd6}#form input{width:150px;height:20px;border-radius:15px;color:black;padding:10px;border:white;margin-right:10px;margin-top:15px}#form button{height:40px;width:120px;border-radius:20px;border:white;cursor:pointer;margin-top:15px}@media (min-width: 768px) and (orientation: portrait){#form{width:100%;height:150px}#form input{width:190px;height:30px;font-size:20px}#form button{height:50px;width:190px;font-size:20px}}@media (min-width: 768px) and (orientation: landscape){#form{width:100%;height:150px}#form input{width:190px;height:20px;font-size:20px}#form button{height:40px;width:190px;font-size:20px}}
