.sevenDays{width:100%;height:1350px;background-image:url("/public/background/sky-34536_640.png");background-position:center;background-size:cover;background-repeat:no-repeat;font-size:16px}.sevenDays .weather{color:whitesmoke;margin-top:5px;margin-bottom:0px}.sevenDays .weather .one{width:100%;height:110px;background-color:rgba(25,173,199,0.5);border-top:2px red solid}.sevenDays .weather .one .date{position:absolute;margin-left:10px;margin-top:10px}.sevenDays .weather .one .descriptionWeather{position:absolute;margin-top:70px;margin-left:10px}.sevenDays .weather .one img{position:absolute;width:80px;height:80px;margin-left:80px}.sevenDays .weather .one .tempDay{position:absolute;margin-top:70px;margin-left:270px}.sevenDays .weather .notMore{visibility:hidden;width:0px;height:0px}.sevenDays .weather .more{width:100%;height:170px;background-color:rgba(25,173,199,0.5)}.sevenDays .weather .more p{padding-top:5px;margin:0px 0 0 10px;text-align:left}@media (orientation: landscape){.sevenDays .weather .one img{margin-left:-2%}.sevenDays .weather .one .tempDay{margin-left:47%}}@media (min-width: 768px) and (orientation: portrait){.sevenDays{font-size:20px}}@media (min-width: 768px) and (orientation: landscape){.sevenDays{font-size:20px}}@media (min-width: 1024px) and (orientation: landscape){.sevenDays{height:1650px;font-size:25px}.sevenDays .weather .more{height:250px}}
